import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-request',
  templateUrl: './request.component.html',
  styleUrls: ['./request.component.scss']
})
export class RequestComponent implements OnInit {

  form: FormGroup;
  pre: 'Hola, necesito un nuevo anuncio con los siguientes detalles: <br> Nombre: <br> Direccin: <br> Telfono: <br> Correo electrnico:';
  // Horario:
  // Latitud:
  // Longitud:
  // Facebook:
  // Sitio Web:
  // Descripcin:'
  constructor(
    public dialogRef: MatDialogRef<RequestComponent>,
    private fb: FormBuilder,
  ) {
    this.form = this.fb.group({
      mensaje: ['Hola, necesito un nuevo anuncio con los siguientes detalles: \n Nombre: \n Direccin: \n Telfono: \n Correo electrnico: \n Horario: \n Latitud: \n Longitud: \n Facebook: \n Sitio Web: \n Descripcin:'],
    });
  }

  ngOnInit() {
  }
  send() {
    // console.log('form', this.form.value);
    this.dialogRef.close(this.form.value);
  }

}
